import React, { Component, PureComponent, Suspense, lazy } from "react";
import { connect } from 'react-redux';
import Header from "./Header";
import Filters from "./Components/Filters";
import PageHeadSection from './Components/pageHeadSection'
// import TransitionEffects from './Components/Transitions'
// import SubmitPic from './Components/UserComponents/SubmitPic/submitButton'
import { styleMainContainer, customCenterHeight, checkPreview } from '../utils'
import { CUSTOM_SERVER_PATH } from '../constants'
import ErrorFound from './Components/errorFallback'
import OwnBanner from './Components/ownBanner'
import ThemeRoute from './Themes'
import { componentWillAppendToBody } from "react-append-to-body";
const TransitionEffects = lazy(() => import('./Components/Transitions'));
const SubmitPic = lazy(() => import('./Components/UserComponents/SubmitPic/submitButton'));

const MyComponent = ({ children }) => { return children;}
const AppendedMyComponent = componentWillAppendToBody(MyComponent);

class App extends PureComponent {

  // shouldComponentUpdate(nextProps) {
  //   if (nextProps.wall !== this.props.wall) return true; else return false;
  // }

  render() {
    const { wall, webFilters, wallId, postData, loaderData, languageSetting } = this.props;

    const sliderThemes = [16, 47, 55, 50, 61]
    const ShowMore_N_Filter = wall ? sliderThemes.includes(wall.Personalization.commerceTheme) : false;

    const bgstyle = wall ? styleMainContainer(wall.BackgroundImage.path, wall.ThemeRule.transparent, `${CUSTOM_SERVER_PATH}${wall.BackgroundImage.path}${wall.BackgroundImage.newName}`, wall.ThemeRule.backgroundColor) : '';
    



    

    return (wall ?
      <>  <ErrorFound><PageHeadSection wall={wall} /></ErrorFound>

        <div className='tb_app_container' id="scrlBarComanW" style={bgstyle}>
          {!window.isEditor ? <AppendedMyComponent><a href={wall.User.referePageLink} target="_blank" style={{display: 'none'}}>{wall.User.referePage}</a></AppendedMyComponent>:''}
          {wall.CustomBanner.status === 1 && wall.CustomBanner.banner_position === 1 ? <ErrorFound><Header CustomBanner={wall.CustomBanner} CustomBannerImage={wall.CustomBannerImage} LogoImage={wall.LogoImage} /></ErrorFound> : null}

          {wall.UserRule.on_site_upload && wall.Personalization.commerceTheme != `50` && (!window.isEditor || checkPreview())  ? <ErrorFound><Suspense fallback={null}><SubmitPic wallId={wallId}  wall={wall} /></Suspense></ErrorFound> : null}
          {wall.BannerImage.status === 1 ? <ErrorFound><OwnBanner BannerImage={wall.BannerImage} /></ErrorFound> : null}
          {(webFilters && webFilters.length > 0 && !ShowMore_N_Filter && wall.Personalization.filterStatus===1) && <ErrorFound><Filters type={wall.Personalization.filter_type} webFilters={webFilters}  wall={wall} languageSetting={languageSetting}/></ErrorFound>}
          <ErrorFound><Suspense fallback={null}><TransitionEffects><ThemeRoute {...this.props} /></TransitionEffects></Suspense></ErrorFound>
          {wall.CustomBanner.status === 1 && wall.CustomBanner.banner_position === 2 ? <ErrorFound><Header CustomBanner={wall.CustomBanner} CustomBannerImage={wall.CustomBannerImage} LogoImage={wall.LogoImage} /></ErrorFound> : null}
        </div></> : ''
    );
  }
}

const mapStateToProps = state => {
  return {
    appData: state.appData,
    loaderData: state.loaderData,
  }
}

export default connect(mapStateToProps)(App);

